@import url(https://fonts.googleapis.com/css?family=VT323);
@import url(https://fonts.googleapis.com/css?family=Syncopate);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.clock-container {
    font-family: 'Syncopate', sans-serif;
    border: 2px solid rgb(34, 59, 80);
    height: 300px;
    width: 300px;
    border-radius: 40%;
    cursor: pointer;
    position: relative;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}
.digital {
    font-family: 'VT323', monospace;
    color: rgb(42, 233, 3);
    background-color:rgb(27, 26, 26);
    box-shadow: 0px 0px 20px 2px rgb(42, 233, 3);
}

/* DISPLAY TIME */
.digital-time {
    width: 100%; 
    height: 100%; 
    position: relative;
}
    /* COLONS FOR TIME */
.digital-time h6:nth-child(2),
.digital-time h6:nth-child(4) {
    position: relative;
    top: -2px;
    font-size: 22px; 
}
.digital-time h6:nth-child(1), 
.digital-time h6:nth-child(3), 
.digital-time h6:nth-child(5), 
.digital-time h6:nth-child(6) {
    width: 50px;
}
.digital-time h6:nth-child(1) {
    text-align: right;
}
    /* AM or PM */
.digital-time h6:nth-child(6) {
    font-size: 44px;
    position: relative;
    top: -2.5px;
}
.digital-time h6 {
    font-size: 44px;
    display: inline-block;
    letter-spacing: 2px;
}
.time-background {
    border-radius: 10px;
    height: 60px;
    box-shadow: 0px 0px 5px 2px rgb(233,233,233);
}


/* DISPLAY DATE */

.date {
    font-size: 22px;
    position: absolute;
    top: 20px;
    margin: 0;
    padding: 2px;
    box-shadow: 0px 0px 2px 1px rgb(233,233,233);
    border-radius: 5px;
}


.analog {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 20px 2px rgb(0, 0, 0);
}
.inner-clock {
    position: absolute;
    height: 5px;
    width: 5px;
    padding: 0;
    margin: 0;
}
.middle-dot { 
    z-index: 1;
    width: 15px;
    height: 15px;
    background-color: rgb(255, 255, 255);
    display: inline-block;
    position: absolute;
    padding: 0;
    margin: 0; 
    border-radius: 50%;
    box-shadow: 0px 0px 5px 1px rgb(0, 0, 0);
}

/* HANDS */

.hand {
    border-radius: 10px;
    color: rgb(255, 255, 255);
}
.hand h6 {
    text-shadow: 0px 0px 5px rgb(27, 26, 26);
    margin: 0;
    position: absolute;
}

/* SECONDS HAND */
.seconds {
    position: absolute;
    bottom: .5px;
    display: inline-block;
    width: 1px;
    height: 140px;
    background-color: red;
    -webkit-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
    box-shadow: 0px 0px 5px .5px rgb(27, 26, 26);
}
/* MINUTES HAND */
.minutes {
    position: absolute;
    bottom: 2.5px;
    display: inline-block;
    width: 5px;
    height: 140px;
    background-color: rgb(255, 255, 255);
    -webkit-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
    box-shadow: 0px 0px 5px .5px rgb(27, 26, 26);
}
/* HOURS HAND */
.hours {
    position: absolute;
    bottom: 2.5px;
    display: inline-block;
    width: 5px;
    height: 100px;
    background-color:rgb(255, 255, 255);
    -webkit-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
    box-shadow: 0px 0px 5px .5px rgb(27, 26, 26);
}

/* DATE AND MIDDAY */
.date-period {
    padding: 2px;
    letter-spacing: 2px;
    font-size: 14px;
    color: rgb(255, 255, 255);
    text-shadow: -1px 1px 0 rgb(27, 26, 26), 
                 1px 1px 0 rgb(27, 26, 26),
                 1px -1px 0 rgb(27, 26, 26),
                 -1px -1px 0 rgb(27, 26, 26);
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.055);
}



.App {
  width: 50%;
  height: 100vh;
  margin: 0 auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;

}




